import React from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron'
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";


function Hero(props){
    return(
        <Jumbotron className="bg-transparent jumbotron-fluid p-0">
            <Container fluid={true}>
                <Row className="justify-content-center py-5">
                    <Col md={8} sm={6}>
                        { props.title && <h1 className="display-1 font-weight-bolder mb-0">{props.title}</h1> }
                        { props.subTitle && <h3 className="display-2 font-weight-light mt-0">{props.subTitle}</h3> }
                        { props.text && <h3 className="lead font-weight-light mt-5" >{props.text}</h3> }
                    </Col>
                </Row>
            </Container>
        </Jumbotron>
    );
}

export default Hero;