import React from 'react';
import {useSpring, animated } from "react-spring";

function RelicInfo (props){

    const style = useSpring({opacity: 1, from: {opacity: 0}});

    return(
        <animated.div className="g-relic-info" style={style}>
            <p className="g-relic-title">{props.title}</p>
            <p className="g-relic-location">{props.location}</p>
            <p className="g-relic-year">{props.year}</p>
        </animated.div>
    );

}

export default RelicInfo;