import React from 'react';

import RelicInfo from "./RelicInfo";

function Relic(props){

    return(
        <div className="d-inline-block g-relic" onClick={(e) => props.click(props.item)}>
            <img className="g-relic-image" src={props.item.imgSrc} alt={props.item.imgSrc}/>
            {props.item.selected && <RelicInfo title={props.item.title} location={props.item.location} year={props.item.year} />}
        </div>
    );

}

export default Relic;