import React from 'react';
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from "./components/Footer";
import Album from "./pages/Album"
import Secret_letter from "./pages/Secret_letter";
import MyForm from "./pages/MyForm";

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state= {
            title: 'manual',
            headerLinks: [
                {title: 'album', path: '/'},
                {title: 'secret_letter', path: '/sl'}
            ],
            album: {
                title: '.rólam',
                subTitle: '.olyan képek',
                text: 'Azért hoztam létre ezt az oldalt, hogy digitális naplóként megosszam azokat, amiket csinálok, amik a fejemben járnak, amik hatással voltak/vannak/lesznek rám. '
            },
            secret_letter: {
                title: 'titkos uzenet',
                text: 'irhatsz nekem szepeket'
            }
        }
    }

    render(){
      return (
          <Router>
            <Container className="p-0" fluid={true}>
                <Navbar className="border-bottom" bg="transparent" expand="lg">
                    <Navbar.Brand > szösszenet. </Navbar.Brand>

                    <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"/>
                    <Navbar.Collapse id="navbar-toggle">
                        <Nav className="ml-auto">
                            <Link className="nav-link" to="/">Album</Link>
                            <Link className="nav-link" to="/sl">Secret Letter</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>

                <Route path="/" exact render={() => <Album title={this.state.album.title} subTitle={this.state.album.subTitle} text={this.state.album.text}/> } />
                <Route path="/sl" exact render={() => <MyForm title={this.state.secret_letter.title} text={this.state.secret_letter.text} />} />

                <Footer/>

            </Container>
          </Router>
      );
  }
}

export default App;
