import React from "react";
import Hero from '../components/Hero';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Content from "../components/Content";

class MyForm extends React.Component {
    constructor(props) {
        super(props);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            status: ""
        };
    }

    render() {
        const { status } = this.state;
        return (
            <div>
                <Hero title={this.props.title} text={this.props.text}/>
                <Content>
                    <Form
                        onSubmit={this.submitForm}
                        action="https://formspree.io/mvowjkkz"
                        method="POST"
                    >
                        <Form.Group>
                            <Form.Label/>
                            <Form.Control type="text" name="message" as="textarea" rows="3"/>
                            {status === "SUCCESS" ? <p className="lead font-weight-light mt-3">Köszönöm :))))</p> : <Button className="d-inline-block mt-2" variant="primary" type="submit" disabled={this.state.disabled}>
                                Küldés
                            </Button>}
                            {status === "ERROR" && <p> A manóba! Valami hiba van, kénytelen leszel rám írni.</p>}
                        </Form.Group>
                    </Form>
                </Content>
            </div>
        );
    }

    submitForm(ev) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) return;
            if (xhr.status === 200) {
                form.reset();
                this.setState({ status: "SUCCESS" });
            } else {
                this.setState({ status: "ERROR" });
            }
        };
        xhr.send(data);
    }
}

export default MyForm;