import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

function Footer(){
    return(

      <footer className="mt-5">
          <Container fluid={true}>
              <Row className="border-top justify-content-center p-3">
                  <Col className="p-0" md={3} sm={12} >
                      <p className="lead font-italic text-md-center">"Mi vagyok én? Senki Pál,</p>
                      <p className="lead font-italic text-md-center m-0">Egy fájó gép, mely pipál."</p>
                      <p className="lead font-italic text-md-right m-2">(1882.)</p>
                  </Col>

              </Row>
          </Container>
      </footer>
    );
}
export default Footer;