import React from 'react';

import goldenGate from '../assets/images/golden-gate.JPG';
import venice from '../assets/images/venice.jpg';
import montenegro from '../assets/images/montenegro.gif';
import camera from '../assets/images/camera.png';
import montenegro_2 from '../assets/images/montenegro_2.gif';
import montenegro_3 from '../assets/images/montenegro_3.jpg';
import orban from '../assets/images/orban.jpg';
import boat from '../assets/images/boat.JPG';
import coin_tower from '../assets/images/coin_tower_3.gif';
import surf_hut from '../assets/images/surf_hut.JPG';
import survivor from '../assets/images/survivor.JPG';
import usa from '../assets/images/usa.JPG';
import yosemite from '../assets/images/yosemite.JPG';


import Relic from "./Relic";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

class Carousel extends React.Component{

    constructor(props){
        super(props);
        this.state={
            items:[
                {
                    id:0,
                    title:'Viktor & Lölő',
                    imgSrc: orban,
                    year: 2018,
                    location:'Budapest',
                    selected: false
                },
                {
                    id:1,
                    title:'Yosemite National Park',
                    imgSrc: yosemite,
                    year: 2019,
                    location:'USA, California',
                    selected: false
                },
                {
                    id:2,
                    title:'Golden Gate Bridge',
                    imgSrc: goldenGate,
                    year: 2019,
                    location:'USA, California',
                    selected: false
                },
                {
                    id:3,
                    title:'Velence',
                    imgSrc: venice,
                    year: 2015,
                    location:'Olaszország',
                    selected: false
                },
                {
                    id:4,
                    title:'Montenegró',
                    imgSrc: montenegro,
                    year: 2016,
                    location:'Montenegró',
                    selected: false
                },
                {
                    id:5,
                    title:'Önlab',
                    imgSrc: camera,
                    year: 2019,
                    location:'',
                    selected: false
                },
                {
                    id:6,
                    title:'Montenegró',
                    imgSrc: montenegro_2,
                    year: 2016,
                    location:'Montenegró',
                    selected: false
                },
                {
                    id:7,
                    title:'Montenegró',
                    imgSrc: montenegro_3,
                    year: 2016,
                    location:'Montenegró',
                    selected: false
                },
                {
                    id:8,
                    title:'Nagymaros',
                    imgSrc: boat,
                    year: 2018,
                    location:'Magyarország',
                    selected: false
                },
                {
                    id:9,
                    title:'Coin-Tower, San Fransisco',
                    imgSrc: coin_tower,
                    year: 2019,
                    location:'USA, California',
                    selected: false
                },
                {
                    id:10,
                    title:'Somewhere, on the West-Coast',
                    imgSrc: surf_hut,
                    year: 2019,
                    location:'USA, California',
                    selected: false
                },
                {
                    id:11,
                    title:'Nevada Fall',
                    imgSrc: survivor,
                    year: 2019,
                    location:'USA, California',
                    selected: false
                },
                {
                    id:12,
                    title:'Lincoln Memorial',
                    imgSrc: usa,
                    year: 2019,
                    location:'USA, Washington D.C.',
                    selected: false
                },


            ]
        }
    }

    handleRelicClick = (id,relic) =>{
        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item =>{
            if(item.id !== id){
                item.selected = false;
            }
        });

        this.setState({
            items
        });

    };

    makeItems = (items) =>{
        return items.map(item => {
            return <Relic item={item} click={(e => this.handleRelicClick(item.id, e))} key={item.id} />
        })
    };

    render(){
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel;